export default {
	data() {
		return {
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
		}
	},
	onLoad(){
		this.loadData();
	},
	methods: {
		async loadData(){
			this.$api.request('category', 'categoryList').then( res => {
				res.data.forEach(item=>{
					//item顶级类目 f一级 s二级 t三级
					this.flist.push(item)
					if (item.childrenList) {
						item.childrenList.forEach(childItem => {
							this.slist.push(childItem)
							childItem.childrenList.forEach(leafItem => {
								this.tlist.push(leafItem)
							})
						})
					}
				}) 
			})
			
		},
		//一级分类点击
		tabtap(item){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			
			this.currentId = item.id;
			let index = this.slist.findIndex(sitem=>sitem.parentId === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e){
			if(!this.sizeCalcState){
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item=>item.top <= scrollTop).reverse();
			if(tabs.length > 0){
				this.currentId = tabs[0].parentId;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			let h = 0;
			this.slist.forEach(item=>{
				let view = uni.createSelectorQuery().select("#main-" + item.id);
				view.fields({
					size: true
				}, data => {
					item.top = h;
					h += data.height;
					item.bottom = h;
				}).exec();
			})
			this.sizeCalcState = true;
		},
		navToList(fid, tid){
			debugger
			uni.navigateTo({
				url: `/pages/product/list?fid=${sid}&tid=${tid}`
			})
		}
	}
}
